
.nft-jacker.jacked {
    .nft-link {
        .jack-history {
            width: 60%;
            max-height: 80vh;
        }
    }
}
.nft-jacker {
    
    .nft-link {
        position: relative;

        .jack-history {

            img.globe {
                position: absolute;
                width: 20px;
                height: 20px;
                bottom: 10px;
                right: 10px;
            }
            position: absolute;
            top: 55px;
            left: 0px;
            width: 100%;
            background-color: #272727db;
            backdrop-filter: blur(4px);
            z-index: 1000000000;
            max-height: 200px;
            display: flex;
            flex-direction: column;
            border: 1px solid #ffffff4a;
            border-radius: 3px;

            .items {
                height: 100%;
                overflow-y: scroll;
                .history-item:hover {
                    background-color: white;
                    color: black;
                }
                .history-item {
                    cursor: pointer;
                    padding: 7px;
                    padding-left: 10px;
                }
            }
        }

    }
}

@media screen and (max-width: 600px) {

    .nft-jacker {
        .nft-link {
            .jack-history {
                width: 90% !important;
            }
        }
    }
}
