
.timer {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    margin-top: 50px;
    position: absolute;
    bottom: 80px;
    left: 0px;
    right: 0px;
    margin-left: auto;
    margin-right: auto;
    z-index: 1;
}

.interval {
    margin: 0 10px;
}

.desc {
    z-index: 1;
    text-align: center;
    visibility: visible;
}

.desc.hide {
    visibility: hidden;
}

.shroomVideo {
    position: absolute;
    z-index: 0;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.stockLeft {
    color: gray;
    text-align: center;
    width: fit-content;
    margin: 0 auto;
    position: absolute;
    bottom: 50px;
    left: 0px;
    right: 0px;
    margin-left: auto;
    margin-right: auto;
    z-index: 1;
}

.stockLeft:hover {
    color: #61fc9f;
}

.nft-jacker.jacked {
    padding: unset;

    .nft-link {
        display: flex;
        flex-direction: row;
        position: fixed;
        top: 23px;
        left: 90px;
        margin-top: unset;

        input {
            max-width: 400px;
        }

        .jack-btn {
            margin-top: 0px;
            margin-left: 10px;
            width: 80px;
            color: white;
            padding: 8px;
        }
    }
}

.nft-jacker {
    font-family: stem;
    background-color: black;
    height: 100%;
    padding: 16%;
    display: flex;
    flex-direction: column;
    z-index: 1;

    h1 {
        font-size: 46pt;
    }

    .back-btn {
        position: absolute;
        left: 25px;
        top: 27px;
        cursor: pointer;
        width: 30px;
        height: 30px;
        background-size: cover;
        background-image: url("/search-icon.svg");
        z-index: 1;
    }

    .error-message {
        margin-left: auto;
        margin-right: auto;
        color: red;
        max-width: 50%;
    }

    .player {
        pointer-events: none;
        opacity: 0.3;
        filter: blur(9px);
    }

    .loaded.player {
        pointer-events: unset;
        opacity: unset;
        filter: unset;
    }
    .artist-name {
        padding: 10px;
        position: absolute;
        color: #595959;
        top: 20px;
        right: 20px;
        font-size: 20pt;
        text-align: right;
        z-index: 1;

        a.current-link {
            color: white;
            font-size: 16pt;
        }

        .cover-art {
            margin-top: 30px;
            width: 260px;
            border-radius: 8px;
            border: 1px solid #877c7c;
        }
        
        .song-name {
            color: white;
            font-size: 14pt;
            max-width: 300px;
            margin-left: auto;
        }

        .description {
            margin-top: 20px;
            max-width: 300px;
            color: white;
            font-size: 10pt;
            text-align: left;

        }
    }

    h1 {
        text-align: center;
        //color: #61fc9f;
        color: transparent;
        z-index: 1;
    }

    .player-container.show {
        margin: auto;
        left: 0px;
        top: 0px;
        bottom: 0px;
        right: 0px;
        position: absolute;
        z-index: 1;

        iframe {
            margin: auto;
            position: absolute;
            left: 0px;
            top: 0px;
            right: 0px;
            bottom: 0px;
        }
    }
    .player-container.hide {
        display: none;
    }
    
    .nft-link.hide {
        display: none;
    }

    iframe {
        border-radius: 30px;
    }
    .nft-link {
        margin-top: 5%;
        margin-left: auto;
        margin-right: auto;
        width: 60%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        z-index: 100000;

        input {
            width: 100%;
            color: #61fc9f;
            background-color: #131415;
            border-radius: 8px;
            outline: none !important;
            border: 1px solid #ffffff4a;
            padding: 10px;
        }

        .jack-btn {
            position: relative;
            padding: 12px;
            background-color: white;
            color: black;
            border: 10px;
            border-radius: 15px;
            text-align: center;
            align-items: flex-start;
            font-size: 14pt;
            font-weight: 600;
            width: 220px;
            margin-left: auto;
            margin-right: auto;
            margin-top: 5%;
            cursor: pointer;
        }

        .jack-btn:hover {
            filter: brightness(.9);
        }
        .jack-btn:before {
            position: absolute;
            top: 50%;
            left: 22px;
            width: 24px;
            height: 25px;
            content: "";
            transform: translateY(-50%);
            background-image: url("data:image/svg+xml,%3Csvg width='24' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5 12.5h14M12 5.5l7 7-7 7' stroke='%23000' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
        }
    }

    .helper-icon {
        position: absolute;
        bottom: 25px;
        left: 26px;
        font-size: 42pt;
        color: white;
        z-index: 1000;
        cursor: pointer;
    }

}

.jacked.nft-jacker {

    .player-helper {
        .mock-player {
            transform: translate(0px, -8%);
        }

        .x-button {
            position: absolute;
            right: 25px;
            top: 25px;
            font-size: 32pt;
        }
    }
}


@media screen and (max-width: 600px) {
    .nft-jacker.jacked {
        .timer {
            display: none;
        }
    }
    .nft-jacker.showing-link-input {
        .player-container.show {
            z-index: 10000;
        }

        .artist-name {
            .cover-art {
                animation-duration: 1s;
                margin-top: 100px;
                animation-name: slide-test-artist; 
            }
        }
        .back-btn {
            opacity: 0.1;
            margin-top: 100px;
            animation-duration: 1s;
            animation-name: slide-test-artist; 
        }
        .artist-name {
            opacity: 0.1;
            top: 100px;
            animation-duration: 1s;
            animation-name: slide-artist; 
        }
    }
    .nft-jacker {
        padding: 10%;
        padding-top: 30%;
        .back-btn {
            position: fixed !important;
            top: 20px;
            right: 20px;
            width: 30px;
            left: unset;
        }

        .back-btn:before {
            top: 10px;
            left: 10px;
            transform: translateY(-50%) rotate(180deg) scale(1.2);
        }


        .artist-name {
            top: 10px;
            right: 60px;
            font-size: 14pt;

            .song-name {
                font-size: 11pt;
                margin-top: 6px;
                max-width: 230px;
                line-height: 18px;
            }
            .cover-art {
                border-radius: 0px;
                margin: 0px;
                position: fixed;
                top: 10px;
                left: 10px;
                width: 80px;
            }
        }
        .nft-link {
            margin-top: 20%;
            width: 95% !important;
            left: 10px !important;
        }

        .player-container.show {
            transform: translate(0px, 55px);
        }
    }
}

@keyframes slide-artist {
    from {
        top: 25px;
    }

    to {
        top: 100px;
    }
}

@keyframes slide-test-artist {
    from {
        margin-top: 0px;
    }

    to {
        margin-top: 100px;
    }
}

